import {FormControlLabel, Radio, RadioGroup, TextField} from '@mui/material';
import React, {ReactElement, useContext, useEffect, useState} from 'react';
import {useHistory} from 'react-router-dom';
import Popup from 'reactjs-popup';
import {ProgressContext} from '../../context/progressContext';
import {localStorageApi} from '../../helpers/localStorage';
import {IComponentProps} from '../../types';
import './userData.css';

export default function UserData(): ReactElement {
  const [sex, setSex] = useState('');
  const [date, setDate] = useState<any>();
  const [error, setError] = useState('');

  const {setProgress} = useContext(ProgressContext);

  const history = useHistory();

  useEffect(() => {
    localStorageApi.addCurrentStep('/user-data-birth');
    setProgress(2);
  }, []);

  const handleSubmit = () => {
    if (sex === '') {
      setError('veuillez renseigner les informations');
    } else if (
      new Date(date) > new Date(2004, 12) ||
      new Date(date) < new Date(1958, 1)
    ) {
      setError(`la date de naissance n'est pas correcte`);
    } else {
      setError('');
      localStorage.setItem('sex', sex);
      localStorage.setItem('date', JSON.stringify(date));
      return history.push('/user-age');
    }
  };

  return (
    <div className="user_data">
      <div className="main_content">
        <div className="user_data_text">
          Pour estimer le nombre dannes pendant lesquelles vous pargnerez au
          3me pilier, veuillez renseigner ces informations.
        </div>

        <div className="user_data_inputs_sexe">
          <p className="sexe_lebel">Sexe: </p>
          <div className="sex_radio_item">
            <input
              className="sex_radio_button"
              name="sexe"
              value={sex}
              onChange={e => {
                setSex('Homme');
              }}
              id="sexe_hommo"
              type="radio"
            />
            <label className="user_data_inputs_lebel" htmlFor="sexe_hommo">
              Homme
            </label>
          </div>

          <div className="sex_radio_item">
            <input
              className="sex_radio_button"
              name="sexe"
              value={sex}
              onChange={e => {
                setSex('Femme');
              }}
              id="sexe_femme"
              type="radio"
            />
            <label className="user_data_inputs_lebel" htmlFor="sexe_femme">
              Femme
            </label>
          </div>
        </div>

        <div className="user_data_inputs_birth">
          <p className="age_lebel">ge: </p>

          <input
            className="user_data_input"
            value={date}
            onChange={e => {
              console.log(e.target.value);

              setDate(e.target.value);
            }}
            // onKeyDown={e => e.preventDefault()}
            id="age"
            type="date"
            max="2004-12-31"
            min="1958-01-01"
          />
        </div>
      </div>

      <div className="footer_content">
        <div className="text_error">{error}</div>
        <button onClick={handleSubmit} className="next_button button_position">
          Continuer
        </button>
        <Popup
          modal
          trigger={
            <div className="pop_up_triger triger_position">
              Pourquoi ces donnes sont-elles essentielles?
            </div>
          }>
          {(
            close:
              | ((
                  event: React.MouseEvent<HTMLButtonElement, MouseEvent>,
                ) => void)
              | undefined,
          ) => (
            <>
              <button className="close" onClick={close}>
                X
              </button>
              <div className="pop_up">
                <div className="pop_up_title">
                  Pourquoi ces donnes sont-elles essentielles ?
                </div>
                <div className="pop_up_text">
                  Vous avez la possibilit dpargner pour votre troisime
                  pilier jusqu lge de la retraite AVS. Pour les femmes, il
                  est fix  64 ans et pour les hommes,  65 ans. Nous calculons
                  donc la diffrence entre lge officiel de la retraite et le
                  vtre, pour obtenir le nombre dannes durant lesquelles vous
                  pourrez pargner.
                </div>
                <div className="pop_up_text">
                  Vous pouvez cotiser  un 3me pilier  partir de 18 ans 
                  condition que vous disposiez dun revenu soumis  lAVS. Tout
                  individu travaillant et payant ses impts en Suisse peut
                  cotiser au 3me pilier, travailleurs frontaliers inclus.{' '}
                </div>
              </div>
            </>
          )}
        </Popup>
      </div>
    </div>
  );
}
